<template lang="pug">
  div#hero.sellerHero
    div.hero__dim
      div.container
        div.hero__text
          h1.hero__text--title
            |     
            span.hero__title--nbsp &nbsp;&nbsp;
            br
            |    
          p.hero__text--subtitle(
            v-if="this.$route.path === '/'"
          ) {{ serviceEn }} 
          p.hero__text--spantext
            |      ,
            br
            |     
</template>

<script>
import { globalVar } from '@/globalVar'

export default {
  name: 'hero',

  data: () => ({
    serviceEn: globalVar.serviceEn,
  }),

}
</script>



